import React from 'react';
import {inject, observer} from 'mobx-react';
import * as mobx from 'mobx';
import moment from 'moment';
import {downloadFile, download} from "@/utils/download";
import {converSize} from '@/utils/utils';
import DeliveryStore from "@/store/delivery/DeliveryStore";
import {requestPost} from "@/utils/api";
import journalSchemas from "@/utils/Schemas/journal";
import {withRouter} from "react-router-dom";


import {Card, List, Input, Checkbox, Button, Icon, Tooltip, Pagination,Modal,Row,Col  } from 'antd';
import url from 'url';
import Config from "@/config";

const {Search} = Input;
const { Meta } = Card;


@inject('DeliveryStore')
@withRouter
@observer
class Template extends React.Component {
	constructor(props) {
		super(props)
		this.store = this.props.DeliveryStore;
		this.state = {
			modalVisible:false,
			modalTitle:'',
			previewVisible:false,
			previewImage: '',
			name: "",
			filePath: "",
			fileUrl:""
		}
	}

	componentDidMount() {
		let search = this.props.location.search;
		const key = url.parse(search, true).query.key;
		this.loadPageInfo();

	}

	onSearch=(value)=>{
		console.log('value=====>',value)
		if(value===''){
			this.store.tdParmas.pageIndex= 1;
		}
		this.store.getTempleteDocPageList(value);
	}

	loadPageInfo = () =>{
		this.store.getTempleteDocPageList('');
	}

	searchAndMgr=(
		<div>
			<Search
				placeholder="搜索"
				onSearch={(value)=>{this.onSearch(value)}}
				style={{width: 252}}
			/>
			<div className='ml15 dib'><Icon type="setting" /><span className='ml5'>管理项目</span></div>
		</div>
	)

	transFaker = (file) =>{
		let src;
		if (/\.(doc|docx)$/.test(file)) {
			src = './images/u1475.png'
		} else if (/\.(xls|xlsx)$/.test(file)) {
			src = './images/u1473.png'
		} else if (/\.(pdf)$/.test(file)) {
			src = './images/share_pdf.png'
		} else if (/\.(zip|rar)$/.test(file)) {
			src = './images/share_zip.png'
		} else if (/\.(ppt|pptx)$/.test(file)) {
			src= './images/u1474.png'
		} else if(/\.(mp4|rmvb|avi|ts)$/.test(file)){
			src= './images/share_vodie.png'
		} else if(/\.(exe)$/.test(file)){
			src= './images/exe_default.jpg'
		} else if(/\.(bmp|jpg|png|gif|jpeg|webp)$/.test(file)){
			src= './images/share_png.png'
		} else {
			src = './images/defaultFileIcon.jpg'
		}
		return src;
	}

	// 手动下载
	downloadUrl = (item, e) => {
		const {fileUrl, name} = item;
		const isImage = (/\.(doc|docx)$/.test(name) || /\.(xls|xlsx)$/.test(name) || /\.(pdf)$/.test(name) || /\.(zip|rar)$/.test(name)) || (/\.(ppt|pptx)$/.test(name));
		if (isImage) {
			downloadFile(fileUrl, name);  // 直接下载pdf
		} else {
			this.downloadImage(fileUrl, name);
		}
		e.stopPropagation();
	}

	handlePageonChange = (pageNumber) => {
		this.store.tdParmas = Object.assign(mobx.toJS(this.store.tdParmas), {
			pageIndex: pageNumber
		});

		this.store.getTempleteDocPageList(this.store.tdParmas.text)
	}

	getDocumentUrl = (url) => {
		return requestPost('getDocumentUrl', Config.journal.getDocumentUrl, journalSchemas.journal.getDocumentUrl, {url: url})
	}

	showModal = async (item) => {
		this.getDocumentUrl(decodeURIComponent(item.fileUrl)).then((res)=>{
			this.setState({
				modalVisible: true,
				modalTitle: item.name,
				fileUrl:res,
				currentItem:item,
			});
		})

	};

	handleOk = e => {
		console.log(e);
		this.setState({
			modalVisible: false,
		});
	};

	handleCancel = e => {
		console.log(e);
		this.setState({
			modalVisible: false,
			fileUrl:''
		});
	};




	render() {
		const { templateDocList, templateTotalCount, tdParmas } = this.store;
        const{ modalTitle, currentItem } = this.state;
		const modalHeader=(
			(
				<Row >
					<Col span={12} className="leftLabelHeader">
						<p ><span >{modalTitle}</span></p>
					</Col>
					<Col span={12} className="rightLabelHeader">
						<p >
							<Button type='default' icon='download' onClick={(e)=>{this.downloadUrl(currentItem,e)}}  >下载</Button>
						</p>
					</Col>
				</Row>
			)
		)

		return (
			<div className="w80 h" style={{paddingBottom: "30px", margin: "0 auto"}}>
<div className="template_search">
	<Search
		placeholder="输入关键字搜索模板"
		onSearch={(value)=>{this.onSearch(value)}}
		style={{width: 600}}
		// prefix={<Icon type="search" style={{ color: 'rgba(0,0,0,.25)' }} />}
	/>
</div>
				<List
					grid={{
						gutter: 16,
						xs: 4,
						sm: 4,
						md: 4,
						lg: 4,
						xl: 4,
						xxl: 4,
					}}
					style={{marginTop:'40px'}}
					dataSource={templateDocList}
					renderItem={(item) => (
						<List.Item >

							<Card
								onClick={()=>{this.showModal(item)}}
								className='template_card'
								hoverable
								bodyStyle={{padding:0}}
								style={{
									//width: 300,
									paddingLeft:12,
									paddingRight:12,
									paddingTop:12,
									margin:'0 auto'
								}}
								cover={
									<img alt={item.name} src={item.defaultImageUrl}
										 style={{
											 maxWidth: '100%',
											 minHeight: '150px',
											 maxHeight: '150px',
										 }}/>
								}
								actions={[
									<div >
										<div style={{textAlign:'left',paddingLeft:'5px',fontSize:'14px',color:'#555555'}}><span >{item.name}</span><span className='download' style={{float:'right',paddingRight:'5px'}} onClick={(e)=>{this.downloadUrl(item,e)}}>下载</span></div>
										<div style={{textAlign:'left',paddingLeft:'5px', marginTop:'10px',fontSize:'12px'}}><span >大小：{converSize(item.fileSize)}</span><span style={{marginLeft:'3%'}}>上传时间：{moment(item.createTime).format('YYYY-MM-DD')}</span></div>
									</div>
									,
								]}
							>
							</Card>

						</List.Item>
					)}
				/>
				<div className="template_pagination">
					<Pagination pageSize={tdParmas.pageSize} onChange={this.handlePageonChange} current={tdParmas.pageIndex} defaultCurrent={tdParmas.pageIndex} total={templateTotalCount} />
				</div>
				<Modal
					title={modalHeader}
					visible={this.state.modalVisible}
					onOk={this.handleOk}
					onCancel={this.handleCancel}
					footer={null}
					style={{ top: 20 ,height:'calc(100vh - 50px)', minWidth:'1000px'}}
					bodyStyle={{padding:0}}
					width={'calc(100% - 50px)'}
				>
<div style={{height:'calc(100vh - 100px)'}}>
	<iframe
		src={this.state.fileUrl}
		width="100%" height="100%" frameBorder="1" style={{minHeight:'450px'}}></iframe>
</div>
				</Modal>
			</div>
		)
	}
}


export default Template;



